* {
  box-sizing: border-box;
}

body, li, ul, h1, h2, p {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  height: 40px;
}

.header .nav__list {
  text-align: center;
  align-items: center;
  list-style-type: none;
  display: flex;
}

.header .nav__list-item {
  height: 60px;
}

.header .nav__list-item:not(:last-child) {
  margin-right: 20px;
}

.header .nav__link {
  color: #060b35;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s ease-in-out;
  display: block;
}

.header .nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.header .nav__link.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.header .nav__link:hover {
  color: #00acdc;
}

.stars {
  width: 96px;
  height: 16px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:first-child) {
  margin-left: 4px;
}

.stars--0 .stars__star:nth-child(n+1):nth-child(-n), .stars--1 .stars__star:nth-child(n+1):nth-child(-n+1), .stars--2 .stars__star:nth-child(n+1):nth-child(-n+2), .stars--3 .stars__star:nth-child(n+1):nth-child(-n+3), .stars--4 .stars__star:nth-child(n+1):nth-child(-n+4), .stars--5 .stars__star:nth-child(n+1):nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  max-width: 200px;
  padding: 32px 16px 16px;
  transition: transform .5s;
  display: flex;
}

.card__top-img {
  max-width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__top-title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__top-code {
  color: #616070;
  font-size: 10px;
  line-height: 14px;
}

.card__bottom {
  margin-top: 16px;
}

.card__bottom-button {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 38px;
  text-decoration: none;
  transition: background-color .3s ease-in-out;
  display: block;
}

.card__bottom-button:hover {
  color: #00acdc;
  background-color: #fff;
}

.card__bottom-reviews {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__bottom-reviews-count {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.card__bottom-price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  line-height: 18px;
  display: flex;
}

.card__bottom-price-title {
  color: #616070;
  font-size: 12px;
}

.card__bottom-price-number {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__top-title {
  color: #34568b;
}

html {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
